.Container{
     margin-top: 28px;
     width: 100%;
}
.Formik_Container{
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      width: 100%;
}
.Form_Container{
      width: 60%;        
}
.Label_Form{
      width: 100%;   
      font-size: 23px;
      font-family: 'Poppins', sans-serif;   
      margin-top: -25px;
}
.Field_Form{
       border: none;
       width: 100%;
       border-bottom: 2px solid #18d26e;
       background-color: transparent;
       color: #fff;
       font-family: 'Poppins', sans-serif;
       outline: none;
       padding: 2px;
       margin-top: 17px;
}
.Error_Menssage{
       width: 100%; 
       font-size: 12px;
       position: relative;
       bottom: 7px;
       color: red; 
       font-family: 'Poppins', sans-serif;   
}
.Submit_Buttom{
       width: 100%; 
       text-align: center;
       background-color:  #10b35c;
       cursor: pointer;
       font-family: 'Poppins', sans-serif;   
       border-radius: 10px;
       margin-top: 20px;
       font-size: 18px;
       padding: 3px;
       border: 2px solid #11dbcf;
}
.Submit_Buttom:hover{
       background-color:  #077a3d;
}